<template>
  <div class="component-sidebar">
    <ul>
      <li>
        <router-link to="/">首页</router-link>
      </li>
      <li v-for="(menu, index) in menus" :key="index">
        <router-link :to="`/item/${menu.type}`">{{ menu.name }}</router-link>
      </li>
    </ul>
  </div>
</template>

<script>
export default {
  name: 'Sidebar',

  data () {
    return {
      menus: []
    }
  },

  created () {
    this.menus = this.$chartData.map(({ name, type }) => ({ name, type }))
  }
}
</script>

<style lang="less">
.component-sidebar {
  height: 100%;

  ul {
    padding: 0;
    margin: 0;

    li {
      list-style: none;

      a {
        text-decoration: none;
        color: #000;
      }
    }
  }
}
</style>



